import { createAsyncThunk, createSlice, PayloadAction } from '@reduxjs/toolkit';
import { RootState } from '../../app/store';
import { fetchFollowers, fetchFollowing, fetchRepo, fetchUser } from './userSearchAPI';

export interface userState {
  avatar_url: string;
  id: number;
  login: string;
  following?: number | undefined | string; // number | undefined is type from Octokit user following, string used to pass abbrv data K,M,B
  followers?: number | undefined | string; // number | undefined is type from Octokit user followers, string used to pass abbrv data K,M,B
  numOfPage?: number | undefined;
}

export interface userDetail {
  avatar_url: string,
  followers: number,
  following: number,
  id: number,
  location: string | null,
  login: string,
  name: string | null,
  public_repos: number,
  numOfPage?: number | undefined;
}

export interface SearchState {
  login: userDetail[];
  favoriteList: string[];
  userDetailLogin: string;
  userDetailFollowers: userState[];
  userDetailFollowing: userState[];
  status: string
}

export interface repoState {
  name: string,
  stargazers_count?: number | undefined | string,
  forks_count?: number | undefined | string,
  git_url?: string | undefined,
}

export interface UserDetailState {
  login: string,
  favoriteList: string[];
  data: userDetail;
  status: string;
  repositories: repoState[];
  followers: userState[];
  following: userState[];
}

const initUserDetail: userDetail = {
  avatar_url: "",
  followers: 0,
  following: 0,
  id: 0,
  location: "",
  login: "",
  name: "",
  public_repos: 0,
}

const initialState: UserDetailState = {
  login: "",
  favoriteList: [],
  data: initUserDetail,
  status: "pending",
  repositories: [],
  followers: [],
  following: [],
};


export const fetchSearchUser = createAsyncThunk(
  'userSearch/fetchUser',
  async (login: string) => {
    const response = await fetchUser(login);
    return response;
  }
);
export const fetchSearchFollowers = createAsyncThunk(
  'userSearch/fetchFollowers',
  async (login: string) => {
    const response = await fetchFollowers(login);
    return response;
  }
);
export const fetchSearchFollowing = createAsyncThunk(
  'userSearch/fetchFollowing',
  async (login: string) => {
    const response = await fetchFollowing(login);
    return response;
  }
);
export const fetchSearchRepo = createAsyncThunk(
  'userSearch/fetchRepo',
  async (login: string) => {
    const response = await fetchRepo(login);
    return response;
  }
);

export const userSlice = createSlice({
  name: 'user',
  initialState,
  // The `reducers` field lets us define reducers and generate associated actions
  reducers: {
    setLogin: (state, action: PayloadAction<string>) => {
      state.login = action.payload;
    },
    addFavorite: (state, action: PayloadAction<string>) => {
      state.favoriteList = state.favoriteList.filter(item => item !== action.payload);
    },
    removeFavorite: (state, action: PayloadAction<string>) => {
      state.favoriteList.push(action.payload);
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(fetchSearchUser.fulfilled, (state, action) => {
        state.data = action.payload;
      })
      .addCase(fetchSearchUser.pending, (state) => {
      })
      .addCase(fetchSearchFollowers.fulfilled, (state, action) => {
        state.followers = action.payload;
      })
      .addCase(fetchSearchFollowers.pending, (state) => {
      })
      .addCase(fetchSearchRepo.fulfilled, (state, action) => {
        state.repositories = action.payload;
      })
      .addCase(fetchSearchRepo.pending, (state) => {
      })
      .addCase(fetchSearchFollowing.fulfilled, (state, action) => {
        state.following = action.payload;
        state.status = 'resolved';
      })
      .addCase(fetchSearchFollowing.pending, (state) => {
      });
  },
});

export const { setLogin, addFavorite, removeFavorite } = userSlice.actions;

export const selectLogin = (state: RootState) => state.user.login;
export const selectStatus = (state: RootState) => state.user.status;
export const selectFavorite = (state: RootState) => state.user.favoriteList;
export const selectFollowers = (state: RootState) => state.user.followers;
export const selectFollowing = (state: RootState) => state.user.following;
export const selectRepositories = (state: RootState) => state.user.repositories;

export default userSlice.reducer;
