import React, { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import { useAppSelector, useAppDispatch } from '../../app/hooks';
import HomeIcon from '@mui/icons-material/Home';
import { Grid, Typography, Link } from '@mui/material';
import Switch from "react-switch";
import FavoriteIcon from '@mui/icons-material/Favorite';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import ForwardIcon from '@mui/icons-material/Forward';
import { SpinnerCircular } from 'spinners-react';
// import { LazyLoadImage } from 'react-lazy-load-image-component';
import {
    userDetail,
    setLogin,
    selectFollowers,
    selectFollowing,
    fetchSearchUser,
    fetchSearchFollowers,
    fetchSearchFollowing,
    fetchSearchRepo,
    userState,
    selectRepositories,
    repoState,
    selectStatus,
} from './userSearchSlice';
import {
    selectDarkMode,
    selectFavorite,
    addFavorite,
    removeFavorite,
    setDarkMode,
} from '../search/searchSlice';
import { useNavigate } from "react-router-dom";

interface CustomizedState {
    user: userDetail
}

const styleDayMode = {
    color: "black",
    backgroundColor: "white",
}

const styleDarkMode = {
    color: "white",
    backgroundColor: "black",
}

const darkTheme = createTheme({
    palette: {
        mode: 'dark',
    },
});
const lightTheme = createTheme({
    palette: {
        mode: 'light',
    },
});

export function User() {
    const navigate = useNavigate();
    const dispatch = useAppDispatch();
    const location = useLocation();
    const state = location.state as CustomizedState; // Type Casting, then you can get the params passed via router
    const { user } = state;

    const status = useAppSelector(selectStatus);
    const darkMode = useAppSelector(selectDarkMode);
    const favoriteList = useAppSelector(selectFavorite);
    const folowingList = useAppSelector(selectFollowing);
    const followersList = useAppSelector(selectFollowers);
    const repositoriesList = useAppSelector(selectRepositories);

    const [pageType, setPageType] = useState("REPOSITORIES");

    useEffect(() => {
        dispatch(setLogin(user.login));
        dispatch(fetchSearchUser(user.login));
        dispatch(fetchSearchFollowers(user.login));
        dispatch(fetchSearchFollowing(user.login));
        dispatch(fetchSearchRepo(user.login));
    }, [user.login]);

    function toUser(val: userState) {
        navigate('/user', { state: { user: val } });
    }

    // ToDo lazy fetch user follower and following
    const UserCard: React.FC<{ data: userState[], type: string }> = ({ data, type }) => {
        return (
            <Grid item container justifyContent={"space-between"}
                sx={{
                    maxHeight: "55vh",
                    overflowY: "scroll",
                    padding: 1,
                }}
            >
                <Typography>TODO: loading each individual user {type} need lazy load</Typography>
                {data.map((datum: userState) =>
                    //card
                    <Grid item container width={'48%'} key={type + datum.id}
                        sx={{
                            padding: '10px',
                            borderRadius: '5px',
                            boxShadow: darkMode ? '1px 1px 1px 1px rgba(255,255,255,0.5)' : '1px 1px 1px 1px rgba(0,0,0,0.5)',
                            maxHeight: '10vh',
                            marginTop: '5px'
                        }}
                    >
                        {/* link > Grid xs4 + Grid xs7 = break grid style */}
                        <Grid item xs={4}>
                            <Link color="inherit" underline="none" onClick={() => toUser(datum)}>
                                <img alt="" src={datum.avatar_url} width={"100%"}></img>
                            </Link>
                        </Grid>
                        <Grid item xs={7}>
                            <Link color="inherit" underline="none" onClick={() => toUser(datum)}>
                                <Typography sx={{ fontSize: '14px' }}>{datum.login}</Typography>
                                <Typography sx={{ fontSize: '12px' }}>{datum.followers} Followers</Typography>
                                <Typography sx={{ fontSize: '12px' }}>{datum.following} Following</Typography>
                            </Link>
                        </Grid>
                        <Grid item xs={1} color='red'>
                            {favoriteList.includes(datum.login) ?
                                <Link color='inherit'
                                    onClick={() => { dispatch(addFavorite(datum.login)) }}>
                                    <FavoriteIcon />
                                </Link>
                                :
                                <Link color='inherit'
                                    onClick={() => { dispatch(removeFavorite(datum.login)) }}>
                                    <FavoriteBorderIcon />
                                </Link>
                            }
                        </Grid>
                    </Grid>
                )}
            </Grid>
        )
    }

    const RepoCard: React.FC<{ data: repoState[] }> = ({ data }) => {
        return (
            <Grid item container justifyContent={"space-between"}
                sx={{
                    maxHeight: "50vh",
                    overflowY: "scroll"
                }}
            >
                {data.map((datum: repoState) =>
                    //card
                    <Grid item width={'48%'} textAlign="left" key={"repo" + datum.name}
                        sx={
                            darkMode ? {
                                ...styleDarkMode,
                                padding: '10px',
                                borderRadius: '5px',
                                boxShadow: darkMode ? '1px 1px 1px 1px rgba(255,255,255,0.5)' : '1px 1px 1px 1px rgba(0,0,0,0.5)',
                                maxHeight: '10vh',
                                marginTop: '5px'
                            } : {
                                ...styleDayMode,
                                padding: '10px',
                                borderRadius: '5px',
                                boxShadow: darkMode ? '1px 1px 1px 1px rgba(255,255,255,0.5)' : '1px 1px 1px 1px rgba(0,0,0,0.5)',
                                maxHeight: '10vh',
                                marginTop: '5px'
                            }
                        }>
                        <Link color="inherit" underline="none" href={datum.git_url ? "http" + datum.git_url.slice(3) : ""}>
                            <Typography sx={{ fontSize: '14px' }}>{datum.name}</Typography>
                            <Typography sx={{ fontSize: '12px' }}>{datum.stargazers_count} stars</Typography>
                            <Typography sx={{ fontSize: '12px' }}>{datum.forks_count} forks</Typography>
                        </Link>
                    </Grid>
                )}
            </Grid >
        )
    }

    return (
        <ThemeProvider theme={darkMode ? darkTheme : lightTheme}>
            <Grid
                container
                spacing={0}
                alignItems="center"
                justifyContent="center"
                style={
                    darkMode ? {
                        ...styleDarkMode,
                        maxHeight: "100vh",
                    } : {
                        ...styleDayMode,
                        maxHeight: "100vh",
                    }
                }>
                <Grid item container xs={12} sm={6} md={3}
                    justifyContent="space-between" alignItems="center"
                    sx={darkMode ? {
                        ...styleDarkMode,
                        border: '1px solid white',
                        minHeight: '100vh',
                    } :
                        {
                            border: '1px solid black',
                            minHeight: '100vh',
                        }}>
                    <Grid item container xs={12} padding={1} >
                        <Grid item xs={1}>
                            <Link color={"inherit"} onClick={() => navigate(-1)}>
                                <ForwardIcon style={{ transform: 'rotate(180deg)' }} />
                            </Link>
                        </Grid>
                        <Grid item xs={9}>
                            <Link color={"inherit"} onClick={() => navigate("/")}>
                                <HomeIcon />
                            </Link>
                        </Grid>
                        <Grid item xs={2}>
                            <Switch
                                onChange={() => dispatch(setDarkMode(!darkMode))}
                                checked={darkMode}
                                uncheckedIcon
                                checkedIcon
                            />
                        </Grid>
                    </Grid>
                    <Grid item container xs={12} padding={1}>

                    </Grid>
                    <Grid item container xs={12} justifyContent={"center"} alignItems={"center"} textAlign="center" padding={1} sx={{ minHeight: '90vh', maxHeight: '90vh' }}>
                        <Grid xs={12} item>
                            <img alt="" src={user.avatar_url} style={{ borderRadius: "50%", width: "40%" }}></img><br />
                            <Typography>
                                {user.login}
                            </Typography>
                            <Typography>
                                {user.location}
                            </Typography>
                        </Grid>

                        <Grid item container>
                            <Grid item xs={4} textAlign={"center"} color={pageType === "REPOSITORIES" ? "#2276CE" : "grey"}>
                                <Link color={"inherit"} underline={"none"} onClick={() => setPageType("REPOSITORIES")}>
                                    <Typography variant={"subtitle2"} sx={{ fontWeight: "bold" }}>
                                        REPOSITORIES
                                    </Typography>
                                    <Typography variant={"subtitle2"} sx={{ fontWeight: "bold" }}>
                                        ({user.public_repos})
                                    </Typography>
                                </Link>
                            </Grid>

                            <Grid item xs={4} textAlign={"center"} color={pageType === "FOLLOWERS" ? "#2276CE" : "grey"}>
                                <Link color={"inherit"} underline={"none"} onClick={() => setPageType("FOLLOWERS")}>
                                    <Typography variant={"subtitle2"} sx={{ fontWeight: "bold" }}>
                                        FOLLOWERS
                                    </Typography>
                                    <Typography variant={"subtitle2"} sx={{ fontWeight: "bold" }}>
                                        ({user.followers})
                                    </Typography>
                                </Link>
                            </Grid>

                            <Grid item xs={4} textAlign={"center"} color={pageType === "FOLLOWING" ? "#2276CE" : "grey"}>
                                <Link color={"inherit"} underline={"none"} onClick={() => setPageType("FOLLOWING")}>
                                    <Typography variant={"subtitle2"} sx={{ fontWeight: "bold" }}>
                                        FOLLOWING
                                    </Typography>
                                    <Typography variant={"subtitle2"} sx={{ fontWeight: "bold" }}>
                                        ({user.following})
                                    </Typography>
                                </Link>
                            </Grid>
                        </Grid>

                        <div style={{ visibility: status === 'pending' ? 'visible' : 'hidden', position: 'absolute', width: '150px', height: '150px', left: 'calc(50vw - 75px)', top: 'calc(50vh - 75px)' }}>
                            <SpinnerCircular enabled={status === 'pending'} size={'100%'} />
                        </div>

                        {pageType === "REPOSITORIES" ?
                            <RepoCard data={repositoriesList} />
                            :
                            pageType === "FOLLOWERS" ?
                                <UserCard data={followersList} type={"followers"} />
                                :
                                <UserCard data={folowingList} type={"following"} />
                        }
                    </Grid>
                </Grid>
            </Grid >
        </ThemeProvider>
    );
}

export default User;
