import React from 'react';
import { User } from './features/user/User';
import './App.css';
import { Grid } from '@mui/material';

function App() {
  return (
    <Grid
      container
      spacing={0}
      justifyContent="center"
      style={{
        backgroundColor: '#EEEEEE',
      }}>
      <User />
    </Grid>
  );
}

export default App;
