import Switch from "react-switch";
import React, { useEffect } from 'react';
import { SpinnerCircular } from 'spinners-react';
import ClearIcon from '@mui/icons-material/Clear';
import SearchIcon from '@mui/icons-material/Search';
import FavoriteIcon from '@mui/icons-material/Favorite';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import { useAppSelector, useAppDispatch } from '../../app/hooks';
import { Grid, TextField, InputAdornment, IconButton, Typography, Button, Link } from '@mui/material';
import logo from './GitHub-Logo.png';
import { useNavigate } from "react-router-dom";
import { ThemeProvider, createTheme } from '@mui/material/styles';
import {
    selectData,
    selectPage,
    selectStatus,
    selectFavorite,
    selectPageType,
    selectDarkMode,
    selectSearchQuery,
    fetchSearch,
    userState,
    setPage,
    setPageType,
    setDarkMode,
    setSearchQuery,
    addFavorite,
    removeFavorite,
} from './searchSlice';

const styleDayMode = {
    color: "black",
    backgroundColor: "white",
}

const styleDarkMode = {
    color: "white",
    backgroundColor: "black",
}

const darkTheme = createTheme({
    palette: {
        mode: 'dark',
    },
});
const lightTheme = createTheme({
    palette: {
        mode: 'light',
    },
});

export function Search() {
    const dispatch = useAppDispatch();
    const navigate = useNavigate();

    const data = useAppSelector(selectData);
    const page = useAppSelector(selectPage);
    const status = useAppSelector(selectStatus);
    const pageType = useAppSelector(selectPageType);
    const darkMode = useAppSelector(selectDarkMode);
    const favoriteList = useAppSelector(selectFavorite);
    const searchQuery = useAppSelector(selectSearchQuery)

    const pageNav = {
        minWidth: '25px',
        padding: '5px',
        boxShadow: darkMode ? '1px 1px 1px 1px rgba(255,255,255,0.5)' : '1px 1px 1px 1px rgba(0,0,0,0.5)',
        backgroundColor: darkMode ? "black" : "white",
        color: darkMode ? "white" : "black",
    }

    const handleUserKeyPress = (event: KeyboardEvent) => {
        const { key, keyCode } = event;
        if (keyCode === 8) {
            // updateSearch(searchQuery.slice(0,searchQuery.length-1))
            // dispatch(fetchSearch({ searchQuery: val, page, data, status, favoriteList, pageType, darkMode }));
        }
        if (keyCode === 32 || (keyCode >= 65 && keyCode <= 90)) {
            // dispatch(setSearchQuery(searchQuery + key))
            dispatch(fetchSearch({ searchQuery: searchQuery + key, page, data, status, favoriteList, pageType, darkMode }));
            updateSearch(searchQuery + key)
        }
    };

    useEffect(() => {
        window.addEventListener('keydown', handleUserKeyPress);

        return () => {
            window.removeEventListener('keydown', handleUserKeyPress);
        };
    });

    function handleClearClick() {
        dispatch(fetchSearch({ searchQuery: "", page: 1, data, status, favoriteList, pageType, darkMode }));
        dispatch(setSearchQuery(""));
        dispatch(setPage(1));
    };

    function updateSearch(val: string) {
        dispatch(fetchSearch({ searchQuery: val, page, data, status, favoriteList, pageType, darkMode }));
        dispatch(setSearchQuery(val));
        dispatch(setPage(1));
    }

    function handleSetPage(val: number) {
        if (val === page) return;
        dispatch(fetchSearch({ searchQuery, page: val, data, status, favoriteList, pageType, darkMode }));
        dispatch(setPage(val));
    }

    function handlesetTypePage(val: string) {
        if (val === pageType) return;
        dispatch(fetchSearch({ searchQuery, page: 1, data, status, favoriteList, pageType: val, darkMode }));
        dispatch(setPageType(val));
        dispatch(setPage(1));
    }

    function toUser(val: userState) {
        navigate('/user', { state: { user: val } });
    }

    function NavButton() {
        if (data.length === 0) { return <Grid /> }

        let numOfPage: number = 0;
        if (typeof data[0].numOfPage !== "undefined") {
            numOfPage = data[0].numOfPage;
        }
        let arr: number[] = [];

        let min = 1;
        let max = 1;
        if (numOfPage < 5) {
            min = 1;
            max = numOfPage;
        } else if (page <= 3) {
            min = 1;
            max = 5;
        } else {
            min = page + 2 >= numOfPage ? numOfPage - 4 : page - 2;
            max = page + 2 <= numOfPage ? page + 2 : numOfPage;
        }
        for (let i = min; min <= i && i <= max; i++) {
            arr.push(i);
        }

        return (
            <Grid item container
                xs={12}
                justifyContent={"space-around"}
                paddingX={5}
                paddingY={1}
                marginTop={"10px"}
                sx={
                    darkMode ? { ...styleDarkMode, }
                        :
                        { ...styleDayMode, }}>
                {page > 3 &&
                    <Button
                        sx={{
                            ...pageNav,
                            borderRadius: '50%',
                        }}
                        onClick={(e) => handleSetPage(1)}
                    >
                        &lt;&lt;
                    </Button>
                }
                {page > 3 &&
                    <Button
                        sx={{
                            ...pageNav,
                            borderRadius: '50%',
                        }}
                        onClick={(e) => handleSetPage(page - 1)}
                    >
                        &lt;
                    </Button>
                }

                {arr.map((pageNum: number) =>
                    <Button key={"PageNav" + pageNum}
                        sx={{
                            ...pageNav,
                            borderRadius: '5px',
                            boxShadow: darkMode ? '1px 1px 1px 1px rgba(255,255,255,0.5)' : '1px 1px 1px 1px rgba(0,0,0,0.5)',
                            backgroundColor: pageNum === page ? "#1578CD" : darkMode ? "black" : "white",
                            color: pageNum === page ? "white" : darkMode ? "white" : "black",
                        }}
                        onClick={(e) => handleSetPage(pageNum)}
                    >
                        {pageNum}
                    </Button>
                )}
                {page + 3 <= numOfPage &&
                    <Button
                        sx={{
                            ...pageNav,
                            borderRadius: '50%',
                        }}
                        onClick={(e) => handleSetPage(page + 1)}
                    >
                        &gt;
                    </Button>
                }
                {page + 3 <= numOfPage &&
                    <Button
                        sx={{
                            ...pageNav,
                            borderRadius: '50%',
                        }}
                        onClick={(e) => handleSetPage(numOfPage)}
                    >
                        &gt;
                        &gt;
                    </Button>
                }
            </Grid>
        )
    }

    const Card: React.FC<{ datum: userState }> = ({ datum }) => {
        return (
            <Grid item container width={'48%'} key={datum.id}
                sx={{
                    padding: '10px',
                    borderRadius: '5px',
                    boxShadow: darkMode ? '1px 1px 1px 1px rgba(255,255,255,0.5)' : '1px 1px 1px 1px rgba(0,0,0,0.5)',
                    maxHeight: '10vh',
                    marginTop: '5px'
                }}
            >
                {/* link > Grid xs4 + Grid xs7 = break grid style */}
                <Grid item xs={4}>
                    <Link color="inherit" underline="none" onClick={() => toUser(datum)}>
                        <img alt="" src={datum.avatar_url} width={"100%"}></img>
                    </Link>
                </Grid>
                <Grid item xs={7}>
                    <Link color="inherit" underline="none" onClick={() => toUser(datum)}>
                        <Typography sx={{ fontSize: '14px' }}>{datum.login}</Typography>
                        <Typography sx={{ fontSize: '12px' }}>{datum.followers} Followers</Typography>
                        <Typography sx={{ fontSize: '12px' }}>{datum.following} Following</Typography>
                    </Link>
                </Grid>
                <Grid item xs={1} color='red'>
                    {favoriteList.includes(datum.login) ?
                        <Link color='inherit'
                            onClick={() => { dispatch(addFavorite(datum.login)) }}>
                            <FavoriteIcon />
                        </Link>
                        :
                        <Link color='inherit'
                            onClick={() => { dispatch(removeFavorite(datum.login)) }}>
                            <FavoriteBorderIcon />
                        </Link>
                    }
                </Grid>
            </Grid>
        )
    }

    return (
        <ThemeProvider theme={darkMode ? darkTheme : lightTheme}>
            <Grid item container xs={12} sm={6} md={3} alignItems="center"
                sx={
                    darkMode ?
                        {
                            ...styleDarkMode,
                            border: '1px solid white',
                            minHeight: '100vh',
                        }
                        :
                        {
                            ...styleDayMode,
                            border: '1px solid black',
                            minHeight: '100vh',
                        }}>
                <Grid item container xs={12} padding={1}>
                    <Grid item xs={10}>
                        <Typography>{pageType}</Typography>
                    </Grid>
                    <Grid item xs={2}>
                        <Switch
                            onChange={() => dispatch(setDarkMode(!darkMode))}
                            checked={darkMode}
                            uncheckedIcon
                            checkedIcon
                        />
                    </Grid>
                </Grid>
                <Grid item container xs={12} padding={1}>
                    <TextField id="outlined-basic"
                        label="Outlined"
                        variant="outlined"
                        value={searchQuery}
                        fullWidth
                        sx={darkMode ?
                            {
                                ...styleDarkMode,
                            }
                            :
                            {
                                ...styleDayMode,
                                border: "1px solid white"
                            }}
                        // onChange={(e) => updateSearch(e.target.value)}

                        InputProps={{
                            endAdornment: (
                                <InputAdornment position='end'
                                    onClick={(e) => handleClearClick()}
                                >
                                    <IconButton
                                        sx={{
                                            backgroundColor: 'grey!important',
                                            color: 'white',
                                            borderRadius: '50%',
                                            visibility: 'visible',
                                        }}>
                                        <ClearIcon />
                                    </IconButton>
                                </InputAdornment>
                            ),
                        }}
                    />
                </Grid>

                <Grid item container xs={12} justifyContent={"space-between"} padding={1} sx={{ minHeight: '65vh', maxHeight: '65vh' }}>
                    <div style={{ visibility: status === 'pending' ? 'visible' : 'hidden', position: 'absolute', width: '150px', height: '150px', left: 'calc(50vw - 75px)', top: 'calc(50vh - 75px)' }}>
                        <SpinnerCircular enabled={status === 'pending'} size={'100%'} />
                    </div>
                    {
                        status !== 'pending' && searchQuery !== "" && data.length === 0 &&
                        <Grid item container justifyContent={'center'} alignItems={'center'} direction={'column'}>
                            <SearchIcon />
                            <Typography sx={{ fontSize: '14px' }}>No match result found for</Typography>
                            <Typography sx={{ fontSize: '14px', fontWeight: 'bold' }}>{searchQuery}</Typography>
                        </Grid>
                    }
                    {
                        data.length === 0 && searchQuery === "" &&
                        <Grid item container justifyContent={'center'} alignItems={'center'} direction={'column'}>
                            <img alt="" src={logo} style={{ opacity: "0.5", width: "250px" }} />
                            <Typography sx={{ fontSize: '14px' }} textAlign="center" width="65%" paddingTop={1}>
                                Enter GitHub username and search users matching the input like Google Search, click avatars to view more details, including repositories, followers and following</Typography>
                        </Grid>
                    }
                    {data.map((datum: userState) =>
                        //card
                        <Card datum={datum} />
                    )}
                </Grid>
                <NavButton />

                <Grid item container xs={12} padding={1}
                    sx={{ borderTop: darkMode ? 'white 1px solid' : 'black 1px solid' }}>
                    <Grid item xs={6} textAlign={"center"} color={pageType === "Search" ? "blue" : "grey"}>
                        <Link color="inherit" underline="none"
                            onClick={() => { handlesetTypePage("Search") }}>
                            <Typography>Search</Typography>
                            <SearchIcon />
                        </Link>
                    </Grid>
                    <Grid item xs={6} textAlign={"center"} color={pageType === "Favorite" ? "#2276CE" : "grey"}>
                        <Link color="inherit" underline="none"
                            onClick={() => { handlesetTypePage("Favorite") }}>
                            <Typography>Favorite</Typography>
                            <FavoriteIcon />
                        </Link>
                    </Grid>
                </Grid>
            </Grid >
        </ThemeProvider>
    );
}
