import { createAsyncThunk, createSlice, PayloadAction } from '@reduxjs/toolkit';
import { RootState } from '../../app/store';
import { fetchdata } from './searchAPI';
import { loadState } from '../../app/browser-storage'

export interface userState {
  avatar_url: string;
  id: number;
  login: string;
  following?: number | undefined | string; // number | undefined is type from Octokit user following, string used to pass abbrv data K,M,B
  followers?: number | undefined | string; // number | undefined is type from Octokit user followers, string used to pass abbrv data K,M,B
  numOfPage?: number | undefined;
}

export interface userDetail {
  avatar_url: string,
  followers: number,
  following: number,
  id: number,
  location: string | null,
  login: string,
  name: string | null,
  numOfPage?: number | undefined;
}

export interface SearchState {
  searchQuery: string;
  page: number;
  data: userDetail[];
  status: string;
  favoriteList: string[];
  pageType: string;
  darkMode: boolean;
}

export interface UserDetailState {
  login: string,
  data: userDetail[];
  repositories: any[];
  followers: userState[];
  following: userState[];
}

const initialState: SearchState = {
  searchQuery: "",
  page: 1,
  data: [],
  status: "idle",
  // favoriteList: ["MichaelTengganus98"],
  favoriteList: getFavoriteList(),
  pageType: "Search",
  darkMode: false,
};

interface localStorage {
  search: SearchState;
}

function getFavoriteList() {
  let favList: string[] = [];
  const a: localStorage = loadState();

  favList = a.search.favoriteList;
  if (favList && favList.length === 0) {
    favList.push("MichaelTengganus98");
    return favList;
  }
  return favList;
}

export const fetchSearch = createAsyncThunk(
  'search/fetchdata',
  async (state: SearchState) => {
    const response = await fetchdata(state.searchQuery, state.page, state.favoriteList, state.pageType);
    return response;
  }
);

export const searchSlice = createSlice({
  name: 'search',
  initialState,
  // The `reducers` field lets us define reducers and generate associated actions
  reducers: {
    setSearchQuery: (state, action: PayloadAction<string>) => {
      state.searchQuery = action.payload;
    },
    setPage: (state, action: PayloadAction<number>) => {
      state.page = action.payload;
    },
    setPageType: (state, action: PayloadAction<string>) => {
      state.pageType = action.payload;
    },
    setDarkMode: (state, action: PayloadAction<boolean>) => {
      state.darkMode = action.payload;
    },
    addFavorite: (state, action: PayloadAction<string>) => {
      state.favoriteList = state.favoriteList.filter(item => item !== action.payload);
    },
    removeFavorite: (state, action: PayloadAction<string>) => {
      state.favoriteList.push(action.payload);
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(fetchSearch.fulfilled, (state, action) => {
        state.status = 'resolved';
        state.data = action.payload;
      })
      .addCase(fetchSearch.pending, (state) => {
        state.status = 'pending';
      })
  },
});

export const { setSearchQuery, setPage, setPageType, addFavorite, removeFavorite, setDarkMode } = searchSlice.actions;

export const selectSearchQuery = (state: RootState) => state.search.searchQuery;
export const selectData = (state: RootState) => state.search.data;
export const selectPage = (state: RootState) => state.search.page;
export const selectDarkMode = (state: RootState) => state.search.darkMode;
export const selectStatus = (state: RootState) => state.search.status;
export const selectFavorite = (state: RootState) => state.search.favoriteList;
export const selectPageType = (state: RootState) => state.search.pageType;

export default searchSlice.reducer;
