import { Octokit } from "@octokit/core";
import { userState, userDetail } from "./searchSlice";

const octokit = new Octokit({
  auth: ''
})

export const fetchdata = async (query: string, page: number, favoriteList: string[], pageType: string) => {
  let data: userDetail[] = [];
  if (pageType === "Favorite") {
    let tempFavList: string[] = [];
    // search query like
    favoriteList.forEach(element => {
      if (element.indexOf(query) !== -1) tempFavList.push(element);
    });

    const numOfPage = Math.ceil(tempFavList.length / 12);
    let length = page === 1 ? tempFavList.length > 12 ? 12 : tempFavList.length % 13 : tempFavList.length

    // page-1 === array list start from 0, but page start from 1 (12 in loop)
    for (let i = (page - 1) * 12; i < length; i++) {
      const user: userDetail = await fetchUser(tempFavList[i]);
      user.numOfPage = numOfPage;
      data.push(user)
    }
    return data;
  }

  if (query === "") {
    return [];
  }
  let response = await octokit.request('GET /search/users', { q: query, per_page: 12, page: page });
  // {
  //   "message": "Only the first 1000 search results are available",
  //   "documentation_url": "https://docs.github.com/v3/search/"
  // }
  const numOfPage = Math.ceil((response.data.total_count > 1000 ? 1000 : response.data.total_count) / 12);
  const temp: userState[] = response.data.items;
  for (let i = 0; i < temp.length; i++) {
    let user: userDetail = await fetchUser(temp[i].login)
    data[i] = user;
    data[i]['numOfPage'] = numOfPage;
  }
  return data;
}

const fetchUser = async (login: string) => {
  const resp = await octokit.request('GET /users/{username}', {
    username: login
  })
  const user: userDetail = resp.data;
  return user;
}